html {
	font-size: 100%;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	margin: 0;
	padding: 0;
	font-family: 'Baloo Tamma 2', cursive, -apple-system, BlinkMacSystemFont,
		'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background: #222831;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

.row {
	height: 90px;
	text-align: left;
	color: #f9f9f9;
	border-radius: 15px;
	display: grid;
	grid-template-columns: 1fr 30px;
	justify-content: space-between;
	width: 100%;
	align-items: center;
	padding: 0 10px;
	width: 100%;
	margin-bottom: 35px;
	color: #1c2938;
	box-shadow: 1px 1px 6.5px rgba(0, 0, 0, 0.4);
	transition: 0.1s ease-in;
}

.row:hover {
	box-shadow: 1px 3px 6.5px rgba(0, 0, 0, 0.5);
	background: rgb(248, 248, 248);
	transform: translateY(-3px);
}

.row-td {
	cursor: text;
}

.row-icon {
	margin-right: 10px;
	font-size: 18px;
	cursor: pointer;
}

.row-td2 {
	display: flex;
	align-items: center;
	justify-content: center;
}

.row-inp {
	width: 15px;
	height: 15px;
	border-radius: 50%;
	background: #292929;
	color: #f9f9f9;
	cursor: pointer;
}

@media screen and (max-width: 500px) {
	.row {
		height: 70px;
		margin-bottom: 20px;
		font-size: 13.5px;
	}
}

.banner {
	display: flex;
	justify-content: space-between;
	padding: 0 35px;
	align-items: center;
	background: #5433ff; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to left,
		#a5fecb,
		#20bdff,
		#5433ff
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

	height: 70px;
	color: #f9f9f9;
	border-radius: 15px;
	margin-bottom: 35px;
	font-size: 20px;
	transition: 0.4s ease-in;
}

.banner:hover {
	transform: translateY(-3px);
}

.banner-title {
	letter-spacing: 1.5px;
	font-weight: 700;
}

.banner-text {
	font-weight: 600;
	text-align: right;
	color: #292929;
}

.banner-text-icon {
	margin-left: 10px;
	color: #292929;
}

.banner-icon2 {
	color: #fbf4f9;
}

@media screen and (max-width: 500px) {
	.banner {
		padding: 0 5px;
		height: 55px;
		margin-bottom: 20px;
	}

	.banner-title {
		font-size: 14px;
	}

	.banner-text {
		font-size: 14px;
	}
}

.task-creator {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	margin: 0 auto;
	height: 90px;
	width: auto;
}

.creator-inp {
	height: 50px;
	width: 90%;
	border-radius: 15px 0 0 15px;
	border: 0;
	outline: 0;
	padding-left: 25px;
	color: #1f2831;
	font-weight: 400;
	background: #ece9e6; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to left,
		#ffffff,
		#ece9e6
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.creator-inp::-webkit-input-placeholder {
	font-size: 17px;
	opacity: 0.5;
	letter-spacing: 0.5px;
	color: #383e56;
	font-weight: 600;
}

.creator-inp:-ms-input-placeholder {
	font-size: 17px;
	opacity: 0.5;
	letter-spacing: 0.5px;
	color: #383e56;
	font-weight: 600;
}

.creator-inp::-ms-input-placeholder {
	font-size: 17px;
	opacity: 0.5;
	letter-spacing: 0.5px;
	color: #383e56;
	font-weight: 600;
}

.creator-inp::placeholder {
	font-size: 17px;
	opacity: 0.5;
	letter-spacing: 0.5px;
	color: #383e56;
	font-weight: 600;
}

.creator-inp:focus {
	background: #ece9e6; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#ffffff,
		#ece9e6
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.creator-btn {
	width: 135px;
	height: 50px;
	border: 0;
	border-radius: 0 15px 15px 0;
	text-shadow: 0.5px 0px 3px rgba(0, 0, 0, 0.2);
	font-size: 18px;
	font-weight: 700;
	color: #ecfcff;
	cursor: pointer;
	outline: 0;
	background: #0ba360;
	transition: all 0.2s ease-out;
}

.creator-btn:hover {
	background: #55caa5;
}

.creator-btn-icon {
	margin-right: 10px;
	transition: 0.4s ease;
}

.creator-btn-icon:hover {
	transform: rotate(180deg);
}

@media screen and (max-width: 500px) {
	.creator-inp {
		font-size: 12px;
		padding-left: 10px;
	}

	.creator-inp::-webkit-input-placeholder {
		font-size: 12px;
	}

	.creator-inp:-ms-input-placeholder {
		font-size: 12px;
	}

	.creator-inp::-ms-input-placeholder {
		font-size: 12px;
	}

	.creator-inp::placeholder {
		font-size: 12px;
	}

	.creator-btn {
		font-size: 13px;
	}
}

.ctrl {
	background: #0cebeb; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#29ffc6,
		#20e3b2,
		#0cebeb
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

	display: flex;
	align-items: center;
	justify-content: flex-start;
	margin: 30px auto;
	height: 50px;
	padding: 10px 40px;
	border-radius: 15px;
}

.ctrl-inp {
	margin-right: 20px;
	width: 20px;
	height: 20px;
	cursor: pointer;
}

.ctrl-label {
	font-size: 20px;
	font-weight: 500;
}

.ctrl-task-icon {
	margin-left: 10px;
}

@media screen and (max-width: 500px) {
	.ctrl {
		padding: 5px 25px;
		display: flex;
		align-items: center;
	}

	.ctrl-inp {
		width: 16px;
		height: 16px;
		background: #292929;
		margin-right: 10px;
		color: #fffaff;
	}

	.ctrl-label {
		font-size: 16px;
		line-height: 0;
	}

	.ctrl-task-icon {
		margin-left: 5px;
	}
}

.App {
	padding: 35px;
}

.table {
	width: 100%;
	background: #f1f1ed;
	margin-top: 40px;
	border-radius: 15px;
	padding: 20px 35px;
}

.thead-tr {
	display: flex;
	justify-content: space-between;
	font-size: 20px;
	font-weight: 600;
	color: #222831;
	margin-bottom: 20px;
}

.thead-icon {
	margin-right: 10px;
}

.table-row {
	margin-bottom: 20px;
}

.table-complete {
	background: #7dc9fb; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to bottom left,
		#ffffff,
		#6dd5fa,
		#2980b9
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.app-done,
.app-desc {
	transition: all 0.15s ease;
}

.app-done:hover {
	color: rgb(101, 223, 182);
	text-shadow: 0.2px 0 2px rgba(0, 0, 0, 0.1);
}

.app-desc:hover {
	color: #33425b;
}

@media screen and (max-width: 500px) {
	.App {
		padding: 20px 10px;
	}

	.table {
		padding: 15px 8px;
		margin-top: 20px;
	}

	.thead-tr {
		font-size: 13px;
	}
}

